import useValue from "../../../hooks/useValue";
import TContainer from "../../Common/TContainer/TContainer";
import TText from "../../Common/TText";
import BlockTitle from "../../MainPage/BlockTitle";
import MonthControl from "./MonthControl";
import PosterItem from "./PosterItem";
import moment from "moment";
import { useEffect } from "react";
import axios from "../../../lib/axios/axios";
import { API } from "../../../api/api";
import { Loading, Row, Spacer } from "@nextui-org/react";


function PosterPage() {
    const currentMonth = useValue(moment().month());
    const currentYear = useValue(moment().year());
    const isLoading = useValue(true);

    const shows = useValue([]);

    useEffect(() => {
        isLoading.set(true);
        axios
            .get(API.poster + `?year=${currentYear.value}&month=${currentMonth.value + 1}`)
            .then(resp => {
                isLoading.set(false);
                if (resp.status !== 200) {
                    console.log("get poster not 200", resp.status);
                    return;
                }
                shows.set(resp.data.filter(item => moment(item.datetime) >= moment()));
            })
            .catch(err => {
                isLoading.set(false);
                console.log("get poster err", err);
            })
    }, [currentMonth.value, currentYear.value]);

    return (
        <>
            <BlockTitle color={"tblack"}>Афиша</BlockTitle>
            <MonthControl currentMonth={currentMonth} currentYear={currentYear}/>
            <Spacer y={0.5}/>
            {isLoading.value && (
            <Row align="center" justify="center">
                <Loading/>
            </Row>
            )}
            {!isLoading.value && shows.value.map((item, idx) => (<PosterItem id={item.show.id} title={item.show.title} img={item.show.img} datetime={item.datetime} last={idx === shows.value.length - 1}/>))}
            {!isLoading.value && shows.value.length === 0 && (
                <Row align="center" justify="center">
                    <TText light size={35} css={{textAlign: "center"}}>На выбранный месяц нет мероприятий</TText>
                </Row>)}
            {!isLoading.value && shows.value.length !== 0 && (
                <>
                    <Spacer y={0.5}/>
                    <MonthControl currentMonth={currentMonth} currentYear={currentYear}/>
                </>
            )}
            <Spacer y={.5}/>
        </>
    );
}

export default PosterPage;