import { Row, Col, Spacer } from "@nextui-org/react";
import { useParams } from "react-router-dom";
import useFetch from "../../../hooks/useFetch";
import BlockTitle from "../../MainPage/BlockTitle";
import moment from "moment";
import { Clock } from "iconsax-react";
import { TButton } from "../../Common/TButton";
import TText from "../../Common/TText";
import ShowMainInfo from "./ShowMainInfo";
import ShowSchedule from "./ShowSchedule";

function ShowPage() {
    const {showID} = useParams();

    const show = useFetch({
        api: "/shows/" + showID,
    })
    const schedule = useFetch({
        api: "/shows/" + showID + "/schedule",
    })
    const cast = useFetch({
        api: "/shows/" + showID + "/cast",
    })

    return (
        <>
            <BlockTitle color={"tblack"} isLoading={show.isLoading}>{show.data?.title}</BlockTitle>
            <ShowMainInfo show={show}/>
            <ShowSchedule schedule={schedule}/>
        </>
    )
}

export default ShowPage;