import { Row, Grid, Col, Spacer} from "@nextui-org/react";
import { TButton } from "../../Common/TButton";
import TText from "../../Common/TText";
import moment from "moment";
import useValue from "../../../hooks/useValue";
import { useLinkClickHandler, useNavigate } from "react-router-dom";

function PosterItem({id, title, img, datetime, last}) {
    const momentDatetime = moment(datetime);
    const isHover = useValue(false);
    const navigate = useNavigate();

    const onClickBuyButton = e => {
        e.stopPropagination();
    }
    return (
        <Row css={{...styles.container, border: last ? "none" : ""}} onMouseEnter={() => isHover.set(true)} onMouseLeave={() => isHover.set(false)} onClick={() => navigate(`/pages/shows/${id}`)}>
            <img src={img} style={{...styles.containerBackground, opacity: isHover.value ? 1 : 0}}/>
            <Grid.Container css={{h: "100%"}} alignContent="center">
                <Grid xs={3.5}>
                    <Row justify="center">
                        <Col css={styles.datetimeContainer}>
                            <Row><TText css={{...styles.date, color: isHover.value ? "white" : ""}} light>{momentDatetime.format("D MMMM")}</TText></Row>
                            <Spacer y={0.25}/>
                            <Row><TText css={{...styles.time, color: isHover.value ? "white" : "#a5a5a5"}} light>{momentDatetime.format("ddd HH:mm")}</TText></Row>
                        </Col>
                    </Row>
                </Grid>
                <Grid xs={8.5} sm={5.5}>
                    <Row align="center">
                        <TText css={{...styles.title, color: isHover.value ? "white" : ""}}>{title}</TText>
                    </Row>
                </Grid>
                <Grid xs={0} sm>
                    <Row justify="center" align="center" css={styles.buttonContainer}>
                        <TButton css={styles.button} white={isHover.value} transparent onClick={onClickBuyButton}><TText css={styles.buttonText} light>Купить билеты</TText></TButton>
                    </Row>
                </Grid>
            </Grid.Container>

        </Row>
            )
}

const styles = {
    container: {
        position: "relative",
        overflow: "hidden",
        borderBottom: "solid 2px #8f2512",
        cursor: "pointer",
        h: "75px",
        '@xs': {
            height: "100px",
        },
        '@sm': {
            height: "150px",
        },
        '@lg': {
            height: "225px",
        }
    },
    containerBackground: {
        transition: "200ms",
        position: "absolute",
        width: "100%",
        height: "100%",
        objectFit: "cover",
        filter: "brightness(0.7)",
    },
    title: {
        fontSize: "30px",
        pl: "15px",
        '@sm': {
            fontSize: "40px"
        },
        '@lg': {
            fontSize: "60px",
        }
    },
    datetimeContainer: {
        width: "max-content",
    },
    date: {
        fontSize: "25px",
        '@lg': {
            fontSize: "40px"
        }
    },
    time: {
        color: "#a5a5a5",
        fontSize: "20px",
        textTransform: "uppercase",
        '@lg': {
            fontSize: "32px",
        }

    },
    buttonText: {
        '@sm': {
            fontSize: "25px",
        },
        '@lg': {
            fontSize: "32px",
        }
    },
    button: {

        '@sm': {
            w: "150px",
            h: "40px",
        },
        '@lg': {
            w: "245px",
            h: "50px",
        }
    }
}

export default PosterItem;